
jQuery(function() {

    // Menu burger
    $(".header__burger").on("click", function() {
        $(".header__burger, .menu").toggleClass("active");
        $(".body").toggleClass("lock");
    })

    // Steps slider

    
    
    
    // Review slider
    $(".review-slider").slick({
        arrows: false,
        dots: true,
        speed: 1500,
        slidesToShow: 2,
        responsive: [
            {
                breakpoint: 769,
                settings: {
                    centerMode: true,
                    centerPadding: "70px",
                    slidesToShow: 1,
                },
            },
            {
                breakpoint: 501,
                settings: {
                    centerMode: false,
                    slidesToShow: 1,
                    centerPadding: "0px",
                },
            },
        ]
    })
 
})
    
